html, body, .page-wrap, .site-main {
  height: 100%; }

body {
  font-family: "Open Sans", "Helvetica Neue", "Helvetica", Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  position: relative; }

.page-wrap {
  position: relative;
  z-index: 2; }

.container {
  max-width: 1300px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto; }

img {
  vertical-align: middle;
  display: block;
  max-width: 100%;
  width: auto;
  height: auto; }

a {
  text-decoration: none;
  color: #0d62ce; }
  a.btn {
    display: inline-block;
    background-color: #0d62ce;
    padding: 8px 20px;
    color: #fff;
    border-radius: 3px;
    transition: background-color .15s; }
    a.btn:hover {
      background-color: #084086; }

body.modal-active,
body.resume-modal-active {
  overflow: hidden; }

.modal {
  opacity: 0;
  visibility: hidden;
  transition: opacity .2s, visibility .2s;
  position: fixed;
  z-index: 1050;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); }
  .modal.active {
    opacity: 1;
    visibility: visible; }
  .modal .content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    max-height: calc(100% - 120px);
    overflow: scroll;
    margin: auto;
    margin-top: 8vh;
    width: calc(100% - 40px);
    max-width: 1350px;
    background-color: #fff; }
    .modal .content > .inner {
      padding: 60px 30px 30px;
      position: relative; }
    .modal .content .modal-heading {
      text-align: center;
      margin-top: 0;
      margin-bottom: 30px; }
    .modal .content textarea {
      height: 120px; }
      @media (min-height: 900px) {
        .modal .content textarea {
          height: 180px; } }
      @media (min-height: 1000px) {
        .modal .content textarea {
          height: 240px; } }
    .modal .content .close-modal {
      position: absolute;
      top: 5px;
      right: 15px;
      font-size: 32px; }
  .modal.resume-modal .content > .inner {
    padding-top: 30px; }
  .modal .overview {
    margin: 30px 0 40px; }
  .modal .site-image-large {
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5); }

.resume .resume-heading {
  margin: 0 0 10px; }

.resume .download-resume {
  margin: 0 0 30px; }

.resume .tabs {
  padding: 0;
  margin: 0;
  border-bottom: 3px solid #444;
  overflow: hidden; }
  .resume .tabs .tab {
    list-style-type: none;
    border-bottom: 1px solid #444; }
    @media (min-width: 768px) {
      .resume .tabs .tab {
        float: left;
        border-bottom: 0;
        border-right: 2px solid #444; } }
  .resume .tabs .tab-link {
    display: block;
    padding: 10px 15px;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 12px;
    transition: background-color .15s;
    user-select: none; }
    .resume .tabs .tab-link:hover {
      background-color: rgba(0, 0, 0, 0.2); }
    @media (min-width: 768px) {
      .resume .tabs .tab-link {
        padding: 14px;
        font-size: 13px; } }
    @media (min-width: 992px) {
      .resume .tabs .tab-link {
        padding-left: 20px;
        padding-right: 20px;
        font-size: 14px; } }
  .resume .tabs .tab.active .tab-link {
    background-color: #ea6100;
    color: #fff;
    cursor: default; }

.resume .panel {
  display: none;
  padding: 15px 0; }
  .resume .panel.active {
    display: block; }
  .resume .panel .panel-heading {
    padding-bottom: 10px;
    margin-bottom: 40px;
    border-bottom: 1px solid #888; }
  .resume .panel .panel-content {
    max-width: 800px; }
    .resume .panel .panel-content .label {
      font-weight: 700; }

.resume .job-heading,
.resume .education-heading {
  margin-bottom: 0; }

.resume .job-location-time,
.resume .education-location-time {
  margin-top: 0;
  color: #888;
  font-size: 15px;
  font-style: italic; }

.site-main .page-section.secondary {
  /* Background pattern downloaded from Subtle Patterns -- https://www.toptal.com/designers/subtlepatterns/y-so-serious-pattern/ */
  background-image: url(../images/y-so-serious.png);
  background-position: center top;
  background-size: cover;
  text-align: center; }
  .site-main .page-section.secondary > .inner {
    padding: 20px;
    background-color: rgba(0, 65, 111, 0.8);
    color: #fff; }
  .site-main .page-section.secondary .selfie {
    border-radius: 50%;
    border: 5px solid #fff;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
    margin-left: auto;
    margin-right: auto;
    width: 180px;
    height: 180px;
    overflow: hidden;
    background-size: cover;
    background-position: center; }
    @media (min-width: 768px) {
      .site-main .page-section.secondary .selfie {
        padding-bottom: 100%;
        width: auto;
        height: 0; } }
  .site-main .page-section.secondary .name {
    margin-bottom: 5px; }
  .site-main .page-section.secondary .address {
    margin-bottom: 0; }
  .site-main .page-section.secondary .job-title {
    margin-top: 0; }
  .site-main .page-section.secondary a {
    color: #fff;
    text-decoration: underline; }

.site-main .page-section.primary > .inner {
  padding: 20px; }
  @media (min-width: 1200px) {
    .site-main .page-section.primary > .inner {
      padding: 30px; } }

@media (min-width: 768px) {
  .site-main {
    display: flex; }
    .site-main .page-section.secondary {
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: 300px;
      width: 300px; }
      .site-main .page-section.secondary > .inner {
        height: 100%;
        padding: 30px; }
    .site-main .page-section.primary {
      flex-grow: 1;
      flex-shrink: 1;
      flex-basis: auto; }
      .site-main .page-section.primary > .inner {
        height: 100%;
        overflow: auto; } }

.projects > .inner {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); }

.projects .project > .inner {
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
  height: 100%; }

.projects .project .content {
  padding: 20px;
  border-top: 2px solid #ddd; }

.projects .project .title {
  margin-top: 5px;
  margin-bottom: 10px; }

.projects .project .role {
  margin-top: 0;
  margin-bottom: 25px; }

.projects .project .modal-content {
  display: none; }

.role-tag {
  display: inline-block;
  color: #fff;
  padding: 5px 10px;
  font-weight: 700;
  background-color: #ea6100;
  font-size: 0.75em;
  border-radius: 3px; }
